<template>
  <div>Reading page</div>
</template>
<script>
import FirestoreService from "@/services/firestore";

export default {
  data() {
    return {};
  },
  created() {
    // this.initData();
  },
  methods: {
    async initData() {
      const data = require("@/assets/data.json");
      const promises = [];

      data.map((item) => {
        item.level = 5;
        promises.push(FirestoreService.set("mondais", item.id, item));
      });

      await Promise.all(promises);
      this.log(data);
    },
  },
};
</script>
